@font-face {
    font-family: 'Stratos Deliveroo Web';
    src: url('StratosDeliverooWeb-SemiBold.eot');
    src: url('StratosDeliverooWeb-SemiBold.eot?#iefix') format('embedded-opentype'),
        url('StratosDeliverooWeb-SemiBold.woff2') format('woff2'),
        url('StratosDeliverooWeb-SemiBold.woff') format('woff'),
        url('StratosDeliverooWeb-SemiBold.ttf') format('truetype'),
        url('StratosDeliverooWeb-SemiBold.svg#StratosDeliverooWeb-SemiBold') format('svg');
    font-weight: 600;
    font-style: normal;
}

