body {
    font-family: 'Stratos Deliveroo Web';
    font-style:normal;
    font-weight:600;
    background-color: #F4F5F5;
}


h1 {
font-size: 28px;
line-height: 28px;
}

h2 {
    font-family: "IBM Plex Sans";
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
}

p {
    font-family: "IBM Plex Sans";
    font-size: 14px;
    line-height: 19px;
    font-weight: 400;
}

.header {
    width: 80%;
    margin:auto;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-color: white;
    padding: 20px 0
}

.header img {
    height:160px;
    width:370px;
    object-fit: cover;
    margin-left: 10px;
}


.section h1 {
    margin-left:7%;
}

.cards {
    width: 70%;
    margin-left:7%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;

}
.card,
.cardClicked {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    
    width: 355px;
    background-color: white;
    margin: 15px;
    margin-left: 0px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items:center;
            align-items:center;
    border-radius: 5px;

}

.cardClicked {
border-left: solid 5px #4CD9CB;
box-sizing: border-box;
}

.card:hover, .cardClicked:hover{
    cursor: pointer;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}


.card img,
.cardClicked img {
    height: 100px;
    width: 100px;
    object-fit: cover;
    text-align:end;
    margin-right: 10px;
    border-radius:5px;
    

}

.description {
    font-family: "IBM Plex Sans";
    font-size: 14px;
    line-height: 19px;
    font-weight: 400;
padding-left:10px;
padding-right:10px
    
}

.menu {
    display:-webkit-flex;
    display:flex;
    padding-left: 10%;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color:white;
    margin:0;
    
    

    
}

.link {
    list-style: none;
    font-size: 12px;
    margin-right: 10px;
    font-family:"IBM Plex Sans";
    font-weight:300;
    color:#00CCBC;
    
    
   
}

.link:hover{
    cursor: pointer;
}



.menuSection {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    background-color:white;
    border-top: solid 0.5px rgba(179, 168, 168, 0.2);
}
.Basket {
    min-height: 200px;
    font-family:"IBM Plex Sans";
    font-weight: 300;
    position:absolute;
    right:10%;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items:center;
            align-items:center;
    color:#2E3333;

}

button {
    background-color:#B9C3C3;
    color:#8C9B9B;
    font-family:"IBM Plex Sans";
    font-size: 16px;
    line-height: 16px;
    font-weight: 600;
    width: 300px;
    padding:17px;
    margin: 5px;
    border-radius: 5px;
}

.container {
    width:280px;
    padding-bottom:10px;
}

.item{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom:10px;
    
}

.icon{
    min-width:-webkit-fit-content;
    min-width:-moz-fit-content;
    min-width:fit-content;
    padding-right:10px;
}

.empty {
    text-align:center;
    font-family: "IBM Plex Sans";
    color:#BCBDBD;
    margin:auto;
}

.full{
    background-color:#33D6C8;
    color:white;
    border:none;
}

.text {
display:-webkit-flex;
display:flex;
-webkit-justify-content: space-between;
        justify-content: space-between;
padding-top:3px;

}

.intermediate {
    padding-top:10px;
    padding-bottom:10px;
    border-top: solid 0.5px rgba(179, 168, 168, 0.2);
}


.total::before{
    display:block;
    content:"";
    height:10px;
    width:100%;
    border:none;
    box-shadow:0px -3px 6px -2px rgba(8, 8, 8, 0.07);
}

.infomessage{
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
